@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700,900&display=swap);
h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  margin-bottom: 0px; }

.layout {
  position: relative;
  min-height: 100vh;
  overflow-x: hidden; }

.content {
  background: #feffbe;
  height: calc(100vh - 128px); }

.header {
  background: #ebd494;
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  white-space: nowrap; }
  .header .sider-toggle {
    background-color: #ebd494;
    color: #472836;
    word-wrap: break-word;
    border-color: #ebd494;
    padding-right: 32px;
    height: 32px;
    width: 32px; }
    .header .sider-toggle svg {
      margin-right: -32px;
      height: 32px;
      width: 32px; }
  .header .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 25px; }
    .header .logo img {
      height: 50px;
      margin: 0 0 0 20px; }
  .header .navbar {
    display: flex;
    align-items: center; }
    .header .navbar .ant-menu {
      border-bottom: 0px;
      background: rgba(0, 0, 0, 0); }
      .header .navbar .ant-menu .ant-menu-item-active,
      .header .navbar .ant-menu .ant-menu-item-selected {
        border-color: #feffbe; }
    .header .navbar li {
      margin-left: 25px; }

.sider-logo {
  display: flex;
  justify-content: center;
  align-items: center; }

.ant-layout-sider {
  background: #a1cdf4; }
  .ant-layout-sider .ant-menu {
    background: #e6fafc; }
    .ant-layout-sider .ant-menu .ant-menu-item {
      background: #e6fafc;
      border-color: #e6fafc; }
      .ant-layout-sider .ant-menu .ant-menu-item h1 {
        color: #472836; }

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: center;
  background: #472836;
  padding-top: 0px;
  padding-bottom: 0px; }

@media screen and (min-width: 601px) {
  #title-blurb {
    font-size: 40px; }
  #description {
    font-size: 32px; } }

@media screen and (max-width: 600px) {
  #title-blurb {
    font-size: 30px; }
  #description {
    font-size: 20px; } }

.wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-items: center;
  background-image: url(../../static/media/cleanup.94a79b36.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.centered {
  position: relative;
  height: 100%;
  width: 100%; }

#title-blurb {
  color: white;
  font-weight: bold; }

#description {
  color: white; }

#landing-img {
  width: 100%; }

.yee-haw {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-70%, -50%);
          transform: translate(-70%, -50%);
  text-shadow: 2px 2px #000000;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Old versions of Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    supported by Chrome, Opera and Firefox */ }

.ant-modal-title {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  font-size: 24px; }

.map {
  position: relative;
  width: 100%;
  height: 100%; }

.modal {
  top: 20px; }

.modal-img {
  margin-bottom: 24px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }

.details-page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%; }
  .details-page-container h1 {
    text-align: center; }
  .details-page-container .image-description-container {
    width: 100%; }
    .details-page-container .image-description-container p {
      margin: 0; }
    .details-page-container .image-description-container .ant-col {
      display: flex;
      justify-content: center;
      align-items: center; }
  .details-page-container .stats-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%; }
    .details-page-container .stats-container .progress-bar {
      width: 75%; }
  .details-page-container .countdown-container {
    display: flex;
    justify-content: center;
    align-items: center; }

